@import url(https://fonts.googleapis.com/css2?family=Finger+Paint&display=swap);
.checkbox-input{width:15px;height:15px;display:inline-block;border:3px solid var(--primary);border-radius:3px;transition:all 0.2s ease-out}.checkbox-on{background-color:var(--green)}

.select-search{font-family:inherit;cursor:pointer;color:var(--yellow)}.select-search-selected{height:36px;width:151px;border-radius:5px;background-color:var(--primary-dark);position:relative;box-shadow:-2px -2px 12px rgba(255,255,255,0.25),5px 4px 5px rgba(0,0,0,0.25);display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;padding:5px}.select-search-selected-button{cursor:pointer;width:0;height:0;border-top:7px solid transparent;border-bottom:7px solid transparent;border-right:10px solid var(--green);transition:all 0.4s ease-out}.select-search-bar{position:absolute;z-index:1}.select-search-bar .input-box{height:36px;width:151px;border:2px solid var(--primary-dark);background-color:var(--primary);display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:center;align-items:center}.select-search-bar .input-box span{margin:0px 3px}.select-search-bar .input-box input{font-family:inherit;color:var(--primary-darker);width:100%;height:100%;background-color:transparent;outline:none;border:none}.select-search-option{height:36px;width:151px;color:var(--primary-darker);display:grid;place-items:center;background-color:var(--primary)}.select-search-option:hover{background-color:var(--yellow)}.left-arrow{-webkit-transform:rotate(0deg);transform:rotate(0deg)}.down-arrow{-webkit-transform:rotate(-90deg);transform:rotate(-90deg)}.two-rounded{border-radius:5px 5px 0px 0px}

.gnode{position:absolute;width:100px;height:100px;background-color:var(--primary);border-radius:50%;display:grid;place-items:center;z-index:3;box-shadow:-6px 7px 9px rgba(0,0,0,0.25);transition:background-color 0.6s}.gnode-inner{width:90%;height:90%;background-color:var(--primary-dark);display:grid;place-items:center;border-radius:50%;box-shadow:inset -15px 14px 20px -12px rgba(0,0,0,0.21)}.gnode-inner-content{color:var(--secondary)}.gnode--selected{background-color:var(--yellow);transition:background-color 0s}.gnode--visited{background-color:var(--green);transition:background-color 0s}.gnode--touched{background-color:var(--yellow);transition:background-color 0s}.gnode--root{background-color:var(--red);transition:background-color 0s}.gnode--root span{color:var(--primary-darker)}.gnode--destination{background-color:var(--primary-darker);transition:background-color 0s}

.path{stroke-width:2.5}.path-text{position:absolute;font-size:20px;z-index:1}.path-text:hover{color:var(--red) !important}

.nodemanager{width:100%;height:100%;background-color:var(--secondary);display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row}.nodemanager .left-panel{position:relative;-webkit-flex-basis:15%;flex-basis:15%;min-width:300px;overflow:auto;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.nodemanager .left-panel-start{margin-top:20px;background-color:var(--green)}.nodemanager .left-panel-selection{color:var(--primary-dark);width:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;padding:0px 10px;margin:10px auto}.nodemanager .left-panel-checkbox-wrapper{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;margin-top:20px}.nodemanager .left-panel-checkbox-wrapper label{margin-right:10px}.nodemanager .right-panel{position:relative;background-color:var(--primary);-webkit-flex-basis:85%;flex-basis:85%;overflow:auto}.nodemanager .right-panel .path{position:absolute}.small-box{height:36px;width:120px;border-radius:5px;font-size:18px;color:var(--primary-dark);font-family:inherit;border:none;outline:none;text-align:center;margin:10px;padding:none;box-shadow:-2px -2px 12px rgba(255,255,255,0.25),5px 4px 5px rgba(0,0,0,0.25)}button.small-box:active,input.small-box:focus{box-shadow:inset -2px -2px 12px rgba(255,255,255,0.25),inset 5px 4px 5px rgba(0,0,0,0.25)}

:root{--primary: #08b2e3;--primary-dark: #26408b;--primary-darker: #0f084b;--secondary: #f06449;--green: #40f99b;--yellow: #f9f871;--red: #a82d6d}#root{width:100vw;height:100vh;background-color:pink;box-sizing:border-box;font-family:'Finger Paint', cursive}

body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

