.gnode {
  position: absolute;
  width: 100px;
  height: 100px;
  background-color: var(--primary);
  border-radius: 50%;
  display: grid;
  place-items: center;
  z-index: 3;

  box-shadow: -6px 7px 9px rgba(0, 0, 0, 0.25);
  transition: background-color 0.6s;
  &-inner {
    width: 90%;
    height: 90%;
    background-color: var(--primary-dark);
    display: grid;
    place-items: center;
    border-radius: 50%;
    box-shadow: inset -15px 14px 20px -12px rgba(0, 0, 0, 0.21);
    &-content {
      color: var(--secondary);
    }
  }

  &--selected {
    background-color: var(--yellow);
    transition: background-color 0s;
  }

  &--visited {
    background-color: var(--green);
    transition: background-color 0s;
  }

  &--touched {
    background-color: var(--yellow);
    transition: background-color 0s;
  }

  &--root {
    // gnode-inner-content
    span {
      color: var(--primary-darker);
    }

    background-color: var(--red);
    transition: background-color 0s;
  }

  &--destination {
    background-color: var(--primary-darker);
    transition: background-color 0s;
  }
}
